import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_SEACRCHPRODUCTIONORDERS_PAGE,
  API_SEACRCHPRODUCTIONORDERS_CREATE,
  API_SEACRCHPRODUCTIONORDERS_UPDATE,
  API_SEACRCHPRODUCTIONORDERS_UPDATESTATUS,
  API_PRODUCT_INIT,
  API_SEACRCHPRODUCTIONORDERS_EXPORT
} from 'api'
//同步
export const update = object => ({
  type: actionType.SEACRCHPRODUCTIONORDER_UPDATE,
  data: object
})

//异步
// 获取列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.SEACRCHPRODUCTIONORDER_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_SEACRCHPRODUCTIONORDERS_PAGE, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page, size } = reqData.searchParams
      dispatch({
        type: actionType.SEACRCHPRODUCTIONORDER_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          searchResult: data.result.list || []
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.SEACRCHPRODUCTIONORDER_FAIL
      })
    })
}

// 创建/编辑仓库
export const handleAddOrEdit = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.SEACRCHPRODUCTIONORDER_UPDATE,
    data: { modalTuttonLoading: true }
  })
  let api =
    reqData.data && reqData.data.id
      ? API_SEACRCHPRODUCTIONORDERS_UPDATE
      : API_SEACRCHPRODUCTIONORDERS_CREATE
  getData({ api, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.SEACRCHPRODUCTIONORDER_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.SEACRCHPRODUCTIONORDER_FAIL
      })
    })
}

// 启用禁用仓库
export const handleOperator = (reqData, callBack) => dispatch => {
  getData({ api: API_SEACRCHPRODUCTIONORDERS_UPDATESTATUS, ...reqData })
    .then(data => {
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.SEACRCHPRODUCTIONORDER_FAIL
      })
    })
}

// 初始化
export const handleInit = (reqData, callBack) => dispatch => {
  getData({ api: API_PRODUCT_INIT, ...reqData })
    .then(data => {
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.SEACRCHPRODUCTIONORDER_FAIL
      })
    })
}

// 导出
export const handleExport = (reqData, callBack) => dispatch => {
  getData({ api: API_SEACRCHPRODUCTIONORDERS_EXPORT, ...reqData })
    .then(data => {
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.SEACRCHPRODUCTIONORDER_FAIL
      })
    })
}



