import React, { PureComponent } from 'react'
import { Form, Button, Row, Col, DatePicker } from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { CsxSelect } from 'Form'
import { API_SEACRCHPRODUCTIONORDERS_WAREHOUSETYPES } from 'api'
@connect(
  state => ({ ...state.searchProductionOrder }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }
  // 查询数据
  query = () => {
    const { handleGetList, searchParams, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    handleGetList({ searchParams: { ...reqObj, page: 1, size } })
  }

  // 重置
  reset = () => {
    const { update } = this.props
    update({ searchParams: {} })
  }
  render() {
    const { searchParams = {}, initDate } = this.props
    const { reservoirAreaCode } = searchParams
    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>
          <Col span={8}>
            <CsxSelect
              dateApi={API_SEACRCHPRODUCTIONORDERS_WAREHOUSETYPES}
              label='商品名称/编码'
              selectItem={{
                data: reservoirAreaCode,
                key: 'key',
                valueKey: 'value'
              }}
              searchParams={{ type: 'T', operationType: 'S' }}
              onChangeSelect={value => {
                this.onInputChange({ reservoirAreaCode: value })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_SEACRCHPRODUCTIONORDERS_WAREHOUSETYPES}
              label='业态'
              selectItem={{
                data: reservoirAreaCode,
                key: 'key',
                valueKey: 'value'
              }}
              searchParams={{ type: 'T', operationType: 'S' }}
              onChangeSelect={value => {
                this.onInputChange({ reservoirAreaCode: value })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_SEACRCHPRODUCTIONORDERS_WAREHOUSETYPES}
              label='地点编码'
              selectItem={{
                data: reservoirAreaCode,
                key: 'key',
                valueKey: 'value'
              }}
              searchParams={{ type: 'T', operationType: 'S' }}
              onChangeSelect={value => {
                this.onInputChange({ reservoirAreaCode: value })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_SEACRCHPRODUCTIONORDERS_WAREHOUSETYPES}
              label='产线编码'
              selectItem={{
                data: reservoirAreaCode,
                key: 'key',
                valueKey: 'value'
              }}
              searchParams={{ type: 'T', operationType: 'S' }}
              onChangeSelect={value => {
                this.onInputChange({ reservoirAreaCode: value })
              }}
            />
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>送货日期：</label>
              <div className='box' style={{ marginRight: 0 }}>
                <DatePicker
                  onChange={moment => {
                    this.onInputChange({ initDate: moment })
                  }}
                  format={'YYYY-MM-DD'}
                  value={initDate}
                />
              </div>
            </div>
          </Col>
          <Col span={24}>
            <Row type='flex' justify='end'>
              <Button
                type='primary'
                className={style.mr10}
                onClick={this.query}
              >
                查询
              </Button>
              <Button type='primary' onClick={this.reset}>
                重置
              </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



