import React, { PureComponent } from 'react'
import { Modal, message, Button } from 'antd'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const confirm = Modal.confirm
@connect(
  state => ({ ...state.productAttributes }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  handleOperator = (text, record, index, type) => {
    const {
      handleGetList,
      searchParams,
      page,
      size,
      handleOperator,
      selectedRowKeys,
      selectedRows
    } = this.props
    console.log('selectedRowKeys is', selectedRowKeys)
    console.log('selectedRows is', selectedRows)
    confirm({
      title: '温馨提示',
      content: '是否删除该储位？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleOperator(
          {
            data: {
              id: record.id
            },
            urlParams: [record.id]
          },
          () => {
            message.success('操作成功')
            handleGetList({ searchParams: { page, size, ...searchParams } })
          }
        )
      }
    })
  }
  render() {
    return (
      <div className={style['table-tool']}>
        <Button
          type='primary'
          className={style.btn}
          onClick={this.handleOperator}
        >
          提交
        </Button>
      </div>
    )
  }
}



