import React, { PureComponent } from 'react'
import { Modal, Row, Col, Input, Form, Button, message } from 'antd'
import { CsxFormSelect } from 'Form'
import { API_SEACRCHPRODUCTIONORDERS_WAREHOUSETYPES } from 'api'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const FormItem = Form.Item
@connect(
  state => ({ ...state.searchProductionOrder, userLoation: state.userLoation }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount() {
    // 数据初始化
    const { modalData, update, userLoation = {} } = this.props
    let isAllowGoods = false
    // 判断是新增还是编辑
    if (modalData) {
      let reqObj = { ...modalData }
      // 特殊处理数据
      reqObj.reservoirAreaCode = {
        value: reqObj.reservoirAreaCode && reqObj.reservoirAreaCode.toString(),
        children:
          reqObj.reservoirAreaName && reqObj.reservoirAreaName.toString()
      }
      reqObj.storageLocationCategoryCode = {
        value:
          reqObj.storageLocationCategoryCode &&
          reqObj.storageLocationCategoryCode.toString(),
        children:
          reqObj.storageLocationCategoryName &&
          reqObj.storageLocationCategoryName.toString()
      }
      reqObj.storageLocationTypeCode = {
        value:
          reqObj.storageLocationTypeCode &&
          reqObj.storageLocationTypeCode.toString(),
        children:
          reqObj.storageLocationTypeName &&
          reqObj.storageLocationTypeName.toString()
      }

      if (
        reqObj.storageLocationTypeCode &&
        reqObj.storageLocationTypeCode.children &&
        reqObj.storageLocationTypeCode.children == '存储位'
      ) {
        isAllowGoods = true
      }
      update({ modalData: { ...reqObj }, isAllowGoods })
    } else {
      let mixingShipperFlag = false,
        mixingGoodsFlag = false,
        warehouseCode = userLoation.children
      update({
        modalData: { mixingShipperFlag, mixingGoodsFlag, warehouseCode },
        isAllowGoods
      })
    }
  }
  handleSelectChange = (typeSource, data) => {
    const { form } = this.props
    let inputObj = form.getFieldsValue()
    console.log('inputObj in the form is', inputObj)
    console.log('data selected', data)
    console.log('typeSource', typeSource)
    // 区分不同的select回掉方法
    if (typeSource === 'reservoirAreaCode') {
      //
    }
    // 储位编码
    let storageLocationCode = `${(inputObj.reservoirAreaCode &&
      inputObj.reservoirAreaCode.value) ||
      ''}${inputObj.storageLocationAisleon || ''}${inputObj.storageRow ||
      ''}${inputObj.storageColumn || ''}${inputObj.storageLayer || ''}`
    // 容积
    let storageLocationVolume = `${(inputObj.storageLocationLength || 1) *
      (inputObj.storageLocationWidth || 1) *
      (inputObj.storageLocationHeight || 1)}`
    form.setFieldsValue({
      storageLocationCode,
      storageLocationVolume
    })
  }
  handleSubmit = e => {
    const {
      form,
      modalData = {},
      handleAddOrEdit,
      userLoation = {}
    } = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let reqObj = { ...values }
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            if (i == 'reservoirAreaCode') {
              reqObj.reservoirAreaName = reqObj[i].children
            }
            if (i == 'storageLocationCategoryCode') {
              reqObj.storageLocationCategoryName = reqObj[i].children
            }
            if (i == 'storageLocationTypeCode') {
              reqObj.storageLocationTypeName = reqObj[i].children
            }
            reqObj[i] = reqObj[i].value
          }
        }
        reqObj.warehouseCode = userLoation.value
        reqObj.warehouseName = userLoation.children
        let result = {
            data: reqObj
          },
          msg = '创建成功'
        // 判断是否是修改,加ID
        if (modalData && modalData.id) {
          result.data.id = modalData.id
          result.urlParams = [modalData.id]
          msg = '编辑成功'
        }
        handleAddOrEdit({ ...result }, () => {
          message.success(msg)
          const { handleGetList, searchParams, page, size } = this.props
          handleGetList({ searchParams: { page, size, ...searchParams } })
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const { update } = this.props
    update({ isShowModal: false })
  }
  // 改变当前的输入值
  onInputChange(values) {
    console.log('values is ', values)
    const { update } = this.props

    update({ modalData: { ...values } })
  }
  render() {
    const {
      modalTitle,
      form: { getFieldDecorator },
      modalTuttonLoading,
      modalData = {}
    } = this.props

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 }
      }
    }
    const typeOptions = [
      { key: '工时', value: '工时' },
      { key: '机时', value: '机时' },
      { key: '辅料', value: '辅料' }
    ]
    const unitOptions = [{ key: 'H', value: 'H' }, { key: '元', value: '元' }]
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
        width='500px'
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <Row gutter={16}>
            <Col span={20}>
              <FormItem {...formItemLayout} label='地点编码' hasFeedback>
                {getFieldDecorator('reservoirAreaCode', {
                  initialValue: modalData.reservoirAreaCode,
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSelect
                    allowClear={true}
                    dateApi={API_SEACRCHPRODUCTIONORDERS_WAREHOUSETYPES}
                    searchParams={{ type: 'T', operationType: 'S' }}
                    selectItem={{ key: 'key', valueKey: 'value' }}
                    handleSelectChange={this.handleSelectChange}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={20}>
              <FormItem {...formItemLayout} label='产线' hasFeedback>
                {getFieldDecorator('reservoirAreaCode', {
                  initialValue: modalData.reservoirAreaCode,
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSelect
                    allowClear={true}
                    dateApi={API_SEACRCHPRODUCTIONORDERS_WAREHOUSETYPES}
                    searchParams={{ type: 'T', operationType: 'S' }}
                    selectItem={{ key: 'key', valueKey: 'value' }}
                    handleSelectChange={this.handleSelectChange}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={20}>
              <FormItem {...formItemLayout} label='类型' hasFeedback>
                {getFieldDecorator('typeOfMethod', {
                  initialValue: modalData.reservoirAreaCode,
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSelect
                    // dateApi={API_SEACRCHPRODUCTIONORDERS_WAREHOUSETYPES}
                    data={typeOptions}
                    searchParams={{ type: 'T', operationType: 'S' }}
                    selectItem={{ key: 'key', valueKey: 'value' }}
                    handleSelectChange={this.handleSelectChange.bind(
                      this,
                      'reservoirAreaCode'
                    )}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={20}>
              <FormItem {...formItemLayout} label='单位' hasFeedback>
                {getFieldDecorator('typeOfMethod', {
                  initialValue: modalData.reservoirAreaCode,
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSelect
                    // dateApi={API_SEACRCHPRODUCTIONORDERS_WAREHOUSETYPES}
                    data={unitOptions}
                    searchParams={{ type: 'T', operationType: 'S' }}
                    selectItem={{ key: 'key', valueKey: 'value' }}
                    handleSelectChange={this.handleSelectChange.bind(
                      this,
                      'reservoirAreaCode'
                    )}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={20}>
              <FormItem
                // labelCol = {{
                //   xs: {span: 24},
                //   sm: {span: 4}
                // }}
                // wrapperCol = {{
                //   xs: {span: 24},
                //   sm: {span: 20}
                // }}
                {...formItemLayout}
                label='固定价格'
                hasFeedback
              >
                {getFieldDecorator('storageLocationName', {
                  initialValue: modalData.storageLocationName
                })(<Input />)}
              </FormItem>
            </Col>
            <Col span={20}>
              <FormItem
                // labelCol = {{
                //   xs: {span: 24},
                //   sm: {span: 4}
                // }}
                // wrapperCol = {{
                //   xs: {span: 24},
                //   sm: {span: 20}
                // }}
                {...formItemLayout}
                label='成本要素'
                hasFeedback
              >
                {getFieldDecorator('storageLocationName', {
                  initialValue: modalData.storageLocationName
                })(<Input />)}
              </FormItem>
              <FormItem
                // labelCol = {{
                //   xs: {span: 24},
                //   sm: {span: 4}
                // }}
                // wrapperCol = {{
                //   xs: {span: 24},
                //   sm: {span: 20}
                // }}
                {...formItemLayout}
                label='操作人员'
                hasFeedback
              >
                {getFieldDecorator('storageLocationName', {
                  initialValue: modalData.storageLocationName
                })(<Input disabled />)}
              </FormItem>
              <FormItem
                // labelCol = {{
                //   xs: {span: 24},
                //   sm: {span: 4}
                // }}
                // wrapperCol = {{
                //   xs: {span: 24},
                //   sm: {span: 20}
                // }}
                {...formItemLayout}
                label='操作时间'
                hasFeedback
              >
                {getFieldDecorator('storageLocationName', {
                  initialValue: modalData.storageLocationName
                })(<Input disabled />)}
              </FormItem>
            </Col>
          </Row>
          <Row type='flex' justify='center'>
            <Col>
              <Button
                style={{ marginRight: '48px' }}
                onClick={this.handleCloseModal}
              >
                取消
              </Button>
              <Button
                type='primary'
                htmlType='submit'
                loading={modalTuttonLoading}
              >
                保存
              </Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



