import React, { PureComponent } from 'react'
import { Table, Modal, Form, message, Tooltip, InputNumber } from 'antd'
const confirm = Modal.confirm
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { CsxFormSelect } from 'Form'
import { countTableScroll } from 'util'
import style from './style/Table.scss'
const FormItem = Form.Item
@connect(
  state => ({ ...state.searchProductionOrder, userLoation: state.userLoation }),
  mapDispatchToProps
)
class app extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }

  handleOperator(record) {
    const {
      handleGetList,
      searchParams,
      page,
      size,
      handleOperator
    } = this.props
    confirm({
      title: '温馨提示',
      content: '是否删除该储位？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleOperator(
          {
            data: {
              id: record.id
            },
            urlParams: [record.id]
          },
          () => {
            message.success('操作成功')
            handleGetList({ searchParams: { page, size, ...searchParams } })
          }
        )
      }
    })
  }
  linkToDet = record => {
    const { history } = this.props
    return history.push(
      `/dirFactory-setting-center/searchProductionOrder/det/${record.id}?editOrCreate=edit`
    )
  }
  componentDidMount() {
    const { handleGetList, searchParams, page, size, update } = this.props
    handleGetList({ searchParams: { page, size, ...searchParams } })
    update({
      modalData: {
        reservoirAreaCode11: { value: '100', children: '100%' },
        reservoirAreaCode: 80
      }
    })
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {}
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          reqObj[i] = reqObj[i].value
        }
      }
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }
  handleSelectChange = data => {
    const { update } = this.props
    return update({
      modalData: {
        reservoirAreaCode11: data.value,
        reservoirAreaCode: (100 * Number(data ? data.value : 0)) / 100
      }
    })
  }
  render() {
    const { total, page, size, tableLoading } = this.props
    const {
      form: { getFieldDecorator },
      modalData = []
    } = this.props
    let pagination = {
      total,
      showTotal: (total) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['5', '50', '100', '200', '500']
    }
    const typeOptions = [
      { key: '120', value: '120%' },
      { key: '110', value: '110%' },
      { key: '100', value: '100%' },
      { key: '80', value: '80%' },
      { key: '60', value: '60%' },
      { key: '50', value: '50%' }
    ]
    let mockData = [
      {
        id: '1',
        name: '123',
        shipperCode: 'a001',
        storageLocationCode: 123,
        storageLocationName: 222,
        defaultStorageLocationFlagDesc: 333,
        storageLocationTypeName: 444,
        storageLocationCategoryName: 555,
        reservoirAreaCode: 666,
        storageLocationStatusDesc1: 7778,
        storageLocationStatusDesc: 888,
        storageLocationStatusDesc2: 499956
      },
      {
        id: '2',
        name: '123',
        shipperCode: 'a001',
        storageLocationCode: 123,
        storageLocationName: 222,
        defaultStorageLocationFlagDesc: 333,
        storageLocationTypeName: 444,
        storageLocationCategoryName: 555,
        reservoirAreaCode: 666,
        storageLocationStatusDesc1: 7778,
        storageLocationStatusDesc: 888,
        storageLocationStatusDesc2: 499956
      },
      {
        id: '3',
        name: '123',
        shipperCode: 'a001',
        storageLocationCode: 123,
        storageLocationName: 222,
        defaultStorageLocationFlagDesc: 333,
        storageLocationTypeName: 444,
        storageLocationCategoryName: 555,
        reservoirAreaCode: 666,
        storageLocationStatusDesc1: 7778,
        storageLocationStatusDesc: 888,
        storageLocationStatusDesc2: 499956
      }
    ]
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 12 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 }
      }
    }
    const columns = [
      {
        title: '渠道',
        width: 150,
        dataIndex: 'storageLocationCode',
        key: 'storageLocationCode'
      },
      {
        title: (
          <div>
            <Tooltip
              placement='top'
              title={'M端要货的地点编码'}
              trigger='hover'
              overlayClassName={'tootip_in_table'}
            >
              <span>地点编码</span>
              <img
                className={style.img_in_table}
                src='/image/questionTip.png'
              />
            </Tooltip>
          </div>
        ),
        width: 150,
        dataIndex: 'storageLocationCode1',
        key: 'storageLocationCode1'
      },
      {
        title: '商品编码',
        width: 150,
        dataIndex: 'defaultStorageLocationFlagDesc',
        key: 'defaultStorageLocationFlagDesc',
        render: (text) => text
      },
      {
        title: '商品名称',
        width: 150,
        dataIndex: 'defaultStorageLocationFlagDesc11',
        key: 'defaultStorageLocationFlagDesc11',
        render: (text) => text
      },
      {
        title: '单位',
        width: 150,
        dataIndex: 'storageLocationTypeName',
        key: 'storageLocationTypeName'
      },
      {
        title: '产线编码',
        width: 150,
        dataIndex: 'storageLocationTypeName23',
        key: 'storageLocationTypeName23'
      },

      {
        title: (
          <div className='box_in_table'>
            <FormItem {...formItemLayout} label='计划产量' hasFeedback>
              {getFieldDecorator('reservoirAreaCode11', {
                initialValue: modalData.reservoirAreaCode11,
                rules: [
                  {
                    required: true,
                    message: '请填写!'
                  }
                ]
              })(
                <CsxFormSelect
                  // dateApi={API_SEACRCHPRODUCTIONORDERS_WAREHOUSETYPES}
                  data={typeOptions}
                  selectItem={{
                    data: 'reservoirAreaCode11',
                    key: 'key',
                    valueKey: 'value'
                  }}
                  handleSelectChange={this.handleSelectChange}
                />
              )}
            </FormItem>
          </div>
        ),
        width: 200,
        dataIndex: 'defaultStorageLocationFlag',
        key: 'defaultStorageLocationFlag',
        fixed: 'right',
        render: () => (
          <div className='box_in_table'>
            <FormItem hasFeedback>
              {getFieldDecorator('reservoirAreaCode', {
                initialValue: modalData.reservoirAreaCode,
                rules: [
                  {
                    required: true,
                    message: '请填写!'
                  }
                ]
              })(<InputNumber />)}
            </FormItem>
          </div>
        )
      }
    ]

    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={columns}
          dataSource={mockData}
          scroll={{ x: countTableScroll(columns) }}
          onChange={this.onChange}
          pagination={pagination}
        />
      </div>
    )
  }
}
export default Form.create()(app)



